<template>
  <div class="main-container">
    <div class="table-container">
      <el-table :data="pageData.list" height="calc(100vh - 208px)">
        <el-table-column
          prop="seriesName"
          label="服务名称"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="agencyLevelName"
          label="代理级别"
          align="center"
        ></el-table-column>
        <el-table-column prop="totalSetsStr" label="赠送套数" align="center">
        </el-table-column>
        <el-table-column
          prop="usedSets"
          label="已用套数"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="createdTime"
          label="购买时间"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="endTime"
          label="到期时间"
          align="center"
        ></el-table-column>
        <el-table-column label="操作" align="center" width="150px">
          <template #default="{ row }">
            <el-link
              :underline="false"
              type="primary"
              @click="openProductDetail(row)"
              >详情</el-link
            >
          </template>
        </el-table-column>
        <template #empty>
          <TableEmpty empty-text="暂无代理产品"></TableEmpty>
        </template>
      </el-table>
      <!-- 分页 -->
      <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
      ></PagePagination>
    </div>
  </div>
  <!--  产品详情  -->
  <ProductDetailDialog
    v-model:visible="initData.productDetailVisible"
    :productInfo="initData.productInfo"
  ></ProductDetailDialog>
</template>

<script lang="ts" setup>
import { shallowReactive, defineAsyncComponent } from "vue";

import { usePage } from "jz-tool-lib";

import { agentProductApi } from "@api/market-service-api";

const ProductDetailDialog = defineAsyncComponent(
  () =>
    import(
      "@components/market-service/personal-agent/product-detail-dialog.vue"
    )
);

defineOptions({
  name: "marketService.personalAgent",
});

const initData = shallowReactive({
  productInfo: {},
  agentProductDetailList: [],
  productDetailVisible: false,
});

const { pageNumberChange, pageSizeChange, pageData } = usePage({
  listApi: agentProductApi,
  props: {
    pageNumber: "current",
    pageSize: "size",
  },
});
// 打开产品详情弹框
const openProductDetail = (row) => {
  initData.productInfo = row;
  initData.productDetailVisible = true;
};
</script>
<style lang="scss" scoped>
.table-container .el-table {
  margin-top: 0;
}
</style>
